 vào nghỉ chân. Vừa bước vào cổng đền bỗng nghe có tiếng khóc thút thít ở phía trong, anh lấy làm ngạc nhiên vội lần vào thượng điện. Dưới ánh đèn dầu, anh thấy một cô gái trạc mười tám, hai mươi tuổi, bị trói vào cột ở một góc điện. Anh lập tức bước đến cởi trói cho nàng và nói:"),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" – Làm sao cô lại bị trói ngồi ở đây?"),_c('br'),_c('br'),_vm._v(" Cô gái gạt nước mắt nói:"),_c('br'),_c('br'),_vm._v(" – Chàng là ai, ở xứ nào mà không biết đây là đền thờ mãng xà ư?"),_c('br'),_c('br'),_vm._v(" Đoạn cô kể lại những hành động của mãng xà, mỗi năm phải nộp một mạng người ra sao, cuối cùng cô nức nở:"),_c('br'),_vm._v(" – Nhà thiếp có một mẹ một con, nghĩ mình chết đi cũng không tiếc, chỉ thương mẹ già không có ai nuôi. Nhưng thôi chàng hãy đi mau khỏi chốn này, nếu không mãng xà nó đền thì chẳng còn tính mạng."),_c('br'),_c('br'),_vm._v(" – Thế bao giờ thì nó đến đây?"),_c('br'),_c('br'),_vm._v(" – Chỉ nội trong đêm nay!"),_c('br'),_c('br'),_vm._v(" Chàng trẻ tuổi ân cần:"),_c('br'),_c('br'),_vm._v(" – Tôi muốn thay nàng nộp mạng cho mãng xà để nàng được về với mẹ."),_c('br'),_c('br'),_vm._v(" Cô gái đáp:"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("– Thiếp không muốn vì mình mà người khác phải chết. Cứ để mặc thiếp chịu số kiếp thê thảm, tiền oan nghiệp chướng này. Chỉ mong chàng nếu có đi qua làng, hãy làm ơn ghé vào an ủi mẹ già giúp thiếp."),_c('br'),_c('br'),_vm._v(" – Không. Nàng hãy kịp tránh khỏi nơi đây để mặc tôi với con quái vật."),_c('br'),_c('br'),_vm._v(" Rồi rút gươm ra khỏi bao, chàng nói tiếp:"),_c('br'),_c('br'),_vm._v(" – Nó không thể làm hại được tôi đâu. Tôi sẽ bắt cái này ra đối mặt một phen với nó. Nàng hãy chạy về làng mau đi!"),_c('br'),_c('br'),_vm._v(" Nói đoạn, anh dẫn cô gái ra khỏi cánh rừng, rồi trở lại đền nai nịt gọn ghẽ, quyết thức đợi mãng xà."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Vào khoảng nửa đêm, bỗng phía ngoài có tiếng rào rào, cành cây gẫy răng rắc. Một mùi tanh tưởi xông vào mũi đến lộn mửa. Chàng trẻ tuổi đứng dậy rút gươm nấp vào bên thượng điện. Mãng xà quen như mọi khi, từ từ vắt mình qua tường tiến vào. Nhưng nó vừa thò cái đầu vào đền thì chàng trẻ tuổi đã khởi thế công ngay. Lưỡi gươm của anh lóe lên trong đêm tối như chớp giật. Con quái vật bị thương, lao tới toan nuốt sống kẻ thù. Nhưng đường gươm lợi hại của anh đã lại bồi cho nó một nhát thứ hai. Mãng xà đau quá, rống lên, hà hơi phun gió rồi quật đuôi tới tấp làm anh lăn đi mấy vòng. Sắp bị lọt vào miệng quái vật thì may sao, anh đã kịp đứng bật dậy, thủ thế đâm cho nó một mũi thứ ba, nhưng vì đâm mạnh quá nên mũi gươm bị gãy. Anh bèn bồi tiếp mấy nhát, mãng xà bị thương nặng. Cả khúc thân của nó quằn quại mạnh đến nỗi tường và mái đền đổ sụp, gạch ngói lăn xuống rào rào. Anh giơ lưỡi gươm chặt lấy cái đầu con ác thú."),_c('br'),_c('br'),_vm._v("Giết được mãng xà, chàng trẻ tuổi khát nước quá chạy khắp rừng đi tìm suối giải khát, rồi vì mệt quá, anh lần ra bên bờ suối ngủ thiếp một ngày một đêm."),_c('br'),_c('br'),_vm._v(" Trưa hôm sau, một viên quản có phận sự gác ở khu vực này tiến vào đền để dò động tĩnh. Hắn vô cùng kinh ngạc khi thấy mãng xà nằm trên vũng máu, mà cô gái thì không biết đã biến đi đường nào. Nhưng nghĩ đến lời hứa của vua, hắn lập tức xách lấy đầu mãng xà phóng ngựa về kinh lĩnh thưởng. Trông thấy đầu con quái vật, nhà vua rất vui mừng. Vua hỏi:"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}